/* eslint-disable */
import React, { useCallback } from 'react';
import PageLayout from '../page-layout/page-layout';

import { PageHeader } from '../page-header/page-header';
import PageHeaderPic from '../../img/page-header/page-header-2.svg';
import SloganImg from '../../img/page-header/slogan-2.svg';

import {SidebarCategoryFilter} from '../sidebar-category-filter/sidebar-category-filter';
import {CoursesList} from '../courses-list/courses-list';

import { useGetCategoriesQuery } from '../../store/api/categories-slice';
import { useGetCoursesQuery } from '../../store/api/courses-slice';

import Message from '../message/message';

export default function CoursesPage() {
  // console.clear();
  const {
    data: categories,
    isFetching: isCategoriesLoading,
    isError: isCategoriesError,
    refetch: refetchCategories,
  } = useGetCategoriesQuery();
  // console.log(categories);
  // console.log('isCategoriesError: ' + isCategoriesError);
  const {
    data: courses,
    isFetching: isCoursesLoading,
    isError: isCoursesError,
    refetch: refetchCourses,
  } = useGetCoursesQuery();
  // console.log(courses);
  // console.log('isCoursesError: ' + isCoursesError);
  // const isCoursesLoading = false;
  // if(courses) CoursesListData = courses;
  // const {endpoints} = courses;

  // fetch('https://sokratapp.ru/wp-json/wp/v2/categories')
  //   .then((response) => response.json())
  //   .then((posts) => {

  //     CoursesListData = posts;
  //     console.log(CoursesListData);
  //   });

  const refetchAllEndpoints = useCallback(() => {
    refetchCourses();
    refetchCategories();
  }, [
    refetchCourses,
    refetchCategories,
  ]);

  let isFetchingError = isCoursesError || isCategoriesError;

  let content;

  if(isFetchingError) {
    content = 
      <>
        <PageHeader headerPic={ PageHeaderPic } sloganImg={ SloganImg } visibleButtonAdd={false} />
        <Message onButtonClick={refetchAllEndpoints} />;
      </>

  } else {
    content = (
      <>
        <PageHeader headerPic={ PageHeaderPic } sloganImg={ SloganImg } visibleButtonAdd={false} />
        <div className="list-layout container">
          <div className="list-layout__block-filter" aria-label="  ">
            <SidebarCategoryFilter
              blockTitle = ''
              categories= {categories}
              isLoading = {isCategoriesLoading}
              isError={isCoursesError}
            />
          </div>

          <div className="list-layout__block-list block-list">
            <form className="block-list__form" action="">
              <input className="block-list__search" type="search" placeholder="  " />
            </form>

            {/*<div className='block-list__products products products--courses'>*/}
            <CoursesList
              courses={courses}
              isLoading={isCoursesLoading}
            />
            {/*</div>*/}
          </div>
        </div>
      </>
    );
  };

  return (
    <PageLayout>
      {content}
    </PageLayout>
  );
}
