import PropTypes from 'prop-types';
import React from 'react';

export default function PopUpDescr({visible, setVisible, title, children}) {
  const html = document.getElementsByTagName('html')[0];
  visible && html.classList.add('fixed');

  return (
    <section className={visible ? 'popup-confidentiality' : 'popup-confidentiality popup-confidentiality--hidden'} onClick={() => {setVisible(false); html.classList.remove('fixed');}}>
      <div className="popup-confidentiality__block" onClick={(e) => e.stopPropagation()}>
        <h2 className="popup-confidentiality__title">{title}</h2>
        <button className="popup-confidentiality__close" type="button" onClick={() => {setVisible(false); html.classList.remove('fixed');}}/>
        <p className="popup-confidentiality__text">
          {children}
        </p>
      </div>
    </section>
  );
}

PopUpDescr.propTypes = {
  visible: PropTypes.bool.isRequired,
  setVisible: PropTypes.func.isRequired,
  title: PropTypes.string.isRequired,
  children: PropTypes.func.isRequired,
};
