// /* eslint-disable */
import PropTypes from 'prop-types';
import React from 'react';
// import { nanoid } from 'nanoid';
import ContentLoader from 'react-content-loader';
import { SquareLoader, ButtonLoader } from '../loader-main/loader-main';

export function PartnerInfo({database, isLoading}) {

  const content = (
    <div className="instructor__info">
      <div className="instructor__avatar">
        {!database ?
          <SquareLoader /> :
          <img src={database.logo_png} width="373px" height="373px" alt="" />}
      </div>
      <div className="instructor__data">
        {!database ?
          <ContentLoader style={{width: '100%', height: '100%'}}>
            <rect x="0" y="0" rx="10" ry="10" width="900" height="64" />
            <rect x="0" y="84" rx="10" ry="10" width="900" height="64" />
            <rect x="0" y="168" rx="10" ry="10" width="900" height="64" />
            <rect x="0" y="252" rx="10" ry="10" width="900" height="64" />
          </ContentLoader> :
          <>
            <div className="instructor__inner">
              <h2 className="instructor__title">{database.title}</h2>
              <p className="instructor__text">{database.content}</p>
              <button className="instructor__more"> </button>
            </div>

            <h3 className="partner__subtitle"></h3>
            <ul className="partner__list">
              <li className="partner__item">
                <a className="partner__link" href="#">
                   
                  <span className="partner__number">
                    2012
                  </span>
                </a>
              </li>
              <li className="partner__item">
                <a className="partner__link" href="#">
                   
                  <span className="partner__number">
                    1500
                  </span>
                </a>
              </li>
              <li className="partner__item">
                <a className="partner__link" href="#">
                  
                  <span className="partner__number">
                    4.8
                  </span>
                </a>
              </li>
            </ul>

            {/*<h2 className="instructor__title instructor__title--small"></h2>
            <ul className="instructor__list">
              <li className="instructor__item">
                <a className="instructor__link instructor__link--portfolio" href="#">
                  <p className="instructor__information"> </p>
                  <p className="instructor__prompt instructor__prompt--active">{database.teacher_position}</p>
                </a>
              </li>
              <li className="instructor__item">
                <a className="instructor__link instructor__link--experience" href="#">
                  <p className="instructor__information"> <span className="instructor__number instructor__number--center">{database.teacher_experience}</span></p>
                  <p className="instructor__prompt">  </p>
                </a>
              </li>
              <li className="instructor__item">
                <a className="instructor__link instructor__link--simple" href="#">
                   <span className="instructor__number">{database.teacher_raiting}</span>
                </a>
              </li>
            </ul>*/}
          </>}
      </div>
      <div className='instructor__btn'>
        {!database ?
          <ButtonLoader /> :
          <a className=" btn" href="#"> </a>}
      </div>
    </div>
  );

  return content;
}

PartnerInfo.propTypes = {
  database: PropTypes.array.isRequired,
  isLoading: PropTypes.bool.isRequired,
};
