import PropTypes from 'prop-types';
import React, { useState } from 'react';
import NumberFormat from 'react-number-format';
import PopUpDescr from '../popup-descr/popup-descr';


export function CourseInfo({database, isLoading}) {
  const [fullInfoVisible, SetFullInfoVisible] = useState();

  let html;
  if(database) {
    html = (
      <>
        <div className="course__info">
          <h1 className="course__title">{database.title}</h1>
          <p className="course__price">  <NumberFormat value={database.price} displayType={'text'} thousandSeparator=" " suffix={database.type_of_pay} /></p>
          <p className="course__description">{database.excerpt}</p>
          <button class="course__button-more" onClick={()=>{
            SetFullInfoVisible(true);
          }}></button>
        </div>
        <PopUpDescr
          visible={fullInfoVisible}
          setVisible={SetFullInfoVisible}
          title={database.title}
        >
          {database.content}
        </PopUpDescr>
      </>
    )
  }

  return (
    <>{html}</>
  );

}

